/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { ReactNode } from 'react';
import { Container } from 'react-bootstrap';
import { useSpring, animated } from 'react-spring';
import { Color } from 'src/constants';
import Header from 'components/Header';
import Footer from 'components/Footer';
import SEO from 'components/seo';
import { useLubyconWelcomeConsole } from 'src/hooks';

interface Props {
  children: ReactNode;
  pageName: string;
  showHead?: boolean;
  description?: string;
}
const Page = ({ children, pageName, showHead = true, description }: Props) => {
  useLubyconWelcomeConsole();

  const titleAnimation = useSpring({
    from: { opacity: 0, transform: 'translateX(-30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
  });
  const descriptionAnimation = useSpring({
    from: { opacity: 0, transform: 'translateX(30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
  });

  return (
    <div data-layout="page">
      <SEO title={pageName} />
      <Header />
      <div data-page={pageName}>
        <Container css={css`
          margin-top: 3rem;
          margin-bottom: 3rem;
        `}>
          {showHead && (
            <animated.h1 style={titleAnimation} css={css`
              color: ${Color.Grey900};
            `}>
              {pageName}
            </animated.h1>
          )}
          {showHead && description && (
            <animated.p style={descriptionAnimation}>{description}</animated.p>
          )}
        </Container>
        <main>
          {children}
        </main>
      </div>
      <Footer />
    </div>
  )
};

export default Page;
